.tabBar {
  position: relative;
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #e5e5e5;
  background: rgb(251, 251, 251);
  height: 34px;
}

.tabBar .display {
  position: absolute;
  right: 0;
  display: flex;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding: 5px 5px 7px 7px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  margin-top: 2px;
}
.tabBar .display.x {
  background-color: rgb(51, 51, 51);
}
.tabBar .displayOption {
  position: absolute;
  right: 0;
  top: 34px;
  height: 100px;
  width: 206px;
  padding: 16px;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  background: rgb(51, 51, 51)
}
.tabBar .displayOption>.options {
  color: rgb(204, 204, 204);
  margin-bottom: 6px;
  font-size: 12px;
  font-weight: 500;
}
.tabBar .optionWrap {
  display: flex;
}
.tabBar .displayOption .col {
  height: 50px;
  width: 90px;
  border-radius: 2px;
  margin-right: 10px;
  background: rgb(80, 80, 80);
  display: flex;
  justify-content: center;
  align-items: center;
}
.tabBar .displayOption .col.clicked {
background-color: rgb(153, 153, 153)
}
.tabBar .displayOption .col.clicked>.colQuare {
  border:3px solid rgb(255, 255, 255);
}
.tabBar .displayOption .col.clicked>.colQuare>hr {
  border-bottom: 3px solid rgb(255, 255, 255);
}
.tabBar .displayOption .row.clicked>.rowQuare {
  border:3px solid rgb(255, 255, 255);
}
.tabBar .displayOption .col>.colQuare {
  border:3px solid rgb(51, 51, 51);
  width: 30px;
  height: 32px;
  border-radius: 1px;
  position: relative;
}
.tabBar .displayOption .col>.colQuare>hr {
  position: absolute;
  border: none;
  border-bottom: 3px solid rgb(51, 51, 51);
  left: 0;
  right: 0;
  bottom: 3px;
  
}
.tabBar .displayOption .row {
  height: 50px;
  width: 90px;
  border-radius: 2px;
  background: rgb(80, 80, 80);
  display: flex;
  justify-content: center;
  align-items: center; 
}
.tabBar .displayOption .row.clicked {
  background: rgb(153, 153, 153);
}
.tabBar .displayOption .row>.rowQuare {
  border:3px solid rgb(51, 51, 51);
  width: 22px;
  height: 20px;
  border-radius: 1px;
}
.tabBar .display.arrgement {
  padding: 5px 5px 7px 7px;
}
.tabBar .display>.itemWrap {
  margin-right: 2px;
}
.tabBar .display .squareItem {
  border: 4px solid rgb(187, 187, 187);
  border-radius: 2px;
  margin-top: 2px;
}


.tabBar span .icon {
  fill: rgb(187, 187, 187);
  height: 1.4em;
  width: 1.4em;
}

.homeHero .imgWrapper img {
  width: 70%;
}
.loginWrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgb(51, 51, 51);
}
a {
  color: white;
}
.loginWrap .goback {
  position: absolute;
  right: 5px;
  top: 8px;
  
}
.loginWrap .goback .icon {
  font-size: 1.5em;
  fill: aliceblue
}
.loginWrap header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 30px;
}
.loginWrap header .name>img {
  height: 45px;
}
.loginWrap header h2 {
  font-weight: normal;
  color: rgb(153, 153, 153)
}
.loginWrap .box {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid;
  border-radius: 4px;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 80%;
  padding-top: 26px;
  padding-bottom: 26px;
  border-color: rgb(81, 81, 81);
}
.loginWrap .box .icon {
  margin-right: 5px;
}
.loginWrap .validation {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
  margin-top: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.loginWrap .validation>.username>.label {
  margin-left: -22%;
  margin-bottom: 2px;
}

.loginWrap .validation>.password>.label {
  margin-left: -31%;
  margin-bottom: 2px;
}

.loginWrap .validation div {
  text-align: center;
  width: 100%;
}

.loginWrap .validation .password {
  margin: 20px
}

.loginWrap .validation input,
button {
  border: none;
  outline: none;
  width: 80%;
  border-radius: 4px;
  padding: 7px;
}

.loginWrap .validation button {
  color: rgba(255, 255, 255, 0.8);
  background: rgba(234, 76, 136, .9)
}

.loginWrap .validation button:active {
  background: rgba(234, 76, 136, 1)
}

.loginWrap .box .item {
  width: 80%;
  border-radius: 4px;
  text-align: center;
  padding: 7px;
  color: white;
}

.loginWrap .box .item:nth-child(2) {
  margin: 20px;
  background-color: rgb(59, 89, 152)
}

.loginWrap .box .item:nth-child(1) {
  background-color: rgb(29, 161, 242)
}

.loginWrap .box .item:nth-child(3) {
  background-color: rgb(66, 133, 244)
}
